

// Hässlicher Hack, da das Feld sonst mit 300px Breite angezeigt wird.


.ctx\:image {

	picture, figure {
		display: flex;
	}


	&--left { picture, figure {
		justify-content: flex-start;
	} }

	&--right { picture, figure {
		justify-content: flex-end;
	} }

	&--center { picture, figure {
		justify-content: center;
	} }
	

	&__caption {
		color: #555;
		font-size: 13px;
		text-align: center;
	}

	&__warning {
		position: absolute;
		display: grid;
		place-items: center;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;

		label {
			display: flex;
			align-items: center;
			gap: 0.5rem;
			background: var(--wp--preset--color--warning);
			padding: 3px 8px;
			border-radius: 5px;
			font-size: 12px;
		}

		&--fatal {
			label { 
				background: var(--wp--preset--color--error); 
				color: #fff;
			}
			
		}
	}

	&__inspector-warning {
		background: rgb(255,245,203);
		border: 1px solid rgb(255,203,112);
		padding: 3px 8px;
		color: #6e480b;
	}

	&--circle {
		border-radius: 999px;
		aspect-ratio: 1/1 !important;
		object-fit: cover;
	}

	&--shadow {
		box-shadow: 1px 1px 5px rgba(0,0,0,0.5);
	}

	&--border {
		border: 7px solid #fff;
	}

	&--scale {
		transform: scaleX(-1);
	}

	&--rounded {
		border-radius: 20px;
	}
}